'use client'

import { useState } from 'react'
import Layout from '@/components/Layout'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Separator } from '@/components/ui/separator'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Checkbox } from '@/components/ui/checkbox'
import { Badge } from '@/components/ui/badge'
import Image from 'next/image'
import Link from 'next/link'

// Mock order data (would come from cart/context in real app)
const mockOrderItems = [
  {
    id: 1,
    name: 'Vestido Floral Elegante',
    price: 189.90,
    image: 'https://images.pexels.com/photos/1536619/pexels-photo-1536619.jpeg?auto=compress&cs=tinysrgb&w=200',
    size: 'M',
    color: 'Azul',
    quantity: 2
  },
  {
    id: 2,
    name: 'Tênis Esportivo Premium',
    price: 299.90,
    image: 'https://images.pexels.com/photos/1040945/pexels-photo-1040945.jpeg?auto=compress&cs=tinysrgb&w=200',
    size: '39',
    color: 'Preto',
    quantity: 1
  }
]

const mockUser = {
  name: 'Maria Silva',
  email: 'maria@email.com',
  phone: '(11) 99999-9999'
}

export default function CheckoutPage() {
  const [currentStep, setCurrentStep] = useState(1)
  const [paymentMethod, setPaymentMethod] = useState('')
  const [useProfileAddress, setUseProfileAddress] = useState(true)
  const [isProcessing, setIsProcessing] = useState(false)

  // Address form state
  const [address, setAddress] = useState({
    cep: '01234-567',
    street: 'Rua das Flores, 123',
    complement: 'Apto 45',
    neighborhood: 'Centro',
    city: 'São Paulo',
    state: 'SP'
  })

  // Payment form states
  const [cardData, setCardData] = useState({
    number: '',
    name: '',
    expiry: '',
    cvv: '',
    installments: '1'
  })

  const [pixData, setPixData] = useState({
    cpf: ''
  })

  // Calculate totals
  const subtotal = mockOrderItems.reduce((sum, item) => sum + (item.price * item.quantity), 0)
  const shipping = 9.90
  const total = subtotal + shipping

  const handleAddressChange = (field: string, value: string) => {
    setAddress(prev => ({ ...prev, [field]: value }))
  }

  const handleCardChange = (field: string, value: string) => {
    setCardData(prev => ({ ...prev, [field]: value }))
  }

  const searchCEP = async (cep: string) => {
    // Mock CEP search - in real app, integrate with ViaCEP API
    if (cep.length === 9) {
      setAddress(prev => ({
        ...prev,
        street: 'Rua Exemplo, 456',
        neighborhood: 'Bairro Exemplo',
        city: 'São Paulo',
        state: 'SP'
      }))
    }
  }

  const processPayment = async () => {
    setIsProcessing(true)
    
    // Mock payment processing
    setTimeout(() => {
      setIsProcessing(false)
      // Redirect to success page
      window.location.href = '/orders/success'
    }, 3000)
  }

  const steps = [
    { number: 1, title: 'Endereço', completed: currentStep > 1 },
    { number: 2, title: 'Pagamento', completed: currentStep > 2 },
    { number: 3, title: 'Confirmação', completed: false }
  ]

  return (
    <Layout>
      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Progress Steps */}
        <div className="mb-8">
          <div className="flex items-center justify-center space-x-8">
            {steps.map((step, index) => (
              <div key={step.number} className="flex items-center">
                <div className={`flex items-center justify-center w-10 h-10 rounded-full border-2 ${
                  currentStep === step.number
                    ? 'border-red-500 bg-red-500 text-white'
                    : step.completed
                    ? 'border-green-500 bg-green-500 text-white'
                    : 'border-gray-300 text-gray-500'
                }`}>
                  {step.completed ? '✓' : step.number}
                </div>
                <span className={`ml-2 ${
                  currentStep === step.number ? 'text-red-600 font-medium' : 'text-gray-600'
                }`}>
                  {step.title}
                </span>
                {index < steps.length - 1 && (
                  <div className={`w-16 h-0.5 mx-4 ${
                    step.completed ? 'bg-green-500' : 'bg-gray-300'
                  }`} />
                )}
              </div>
            ))}
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Main Content */}
          <div className="lg:col-span-2">
            {/* Step 1: Address */}
            {currentStep === 1 && (
              <Card>
                <CardHeader>
                  <CardTitle>Endereço de Entrega</CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  {/* Use Profile Address Option */}
                  <div className="flex items-center space-x-2">
                    <Checkbox
                      id="useProfile"
                      checked={useProfileAddress}
                      onCheckedChange={(checked) => setUseProfileAddress(checked === true)}
                    />
                    <Label htmlFor="useProfile">
                      Usar endereço do meu perfil
                    </Label>
                  </div>

                  {!useProfileAddress && (
                    <div className="space-y-4">
                      {/* CEP */}
                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <Label htmlFor="cep">CEP</Label>
                          <Input
                            id="cep"
                            value={address.cep}
                            onChange={(e) => {
                              handleAddressChange('cep', e.target.value)
                              if (e.target.value.length === 9) {
                                searchCEP(e.target.value)
                              }
                            }}
                            placeholder="00000-000"
                            maxLength={9}
                          />
                        </div>
                        <div className="flex items-end">
                          <Button 
                            variant="outline" 
                            onClick={() => searchCEP(address.cep)}
                            className="w-full"
                          >
                            Buscar CEP
                          </Button>
                        </div>
                      </div>

                      {/* Street */}
                      <div>
                        <Label htmlFor="street">Endereço</Label>
                        <Input
                          id="street"
                          value={address.street}
                          onChange={(e) => handleAddressChange('street', e.target.value)}
                          placeholder="Rua, número"
                        />
                      </div>

                      {/* Complement and Neighborhood */}
                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <Label htmlFor="complement">Complemento</Label>
                          <Input
                            id="complement"
                            value={address.complement}
                            onChange={(e) => handleAddressChange('complement', e.target.value)}
                            placeholder="Apto, bloco, etc."
                          />
                        </div>
                        <div>
                          <Label htmlFor="neighborhood">Bairro</Label>
                          <Input
                            id="neighborhood"
                            value={address.neighborhood}
                            onChange={(e) => handleAddressChange('neighborhood', e.target.value)}
                            placeholder="Bairro"
                          />
                        </div>
                      </div>

                      {/* City and State */}
                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <Label htmlFor="city">Cidade</Label>
                          <Input
                            id="city"
                            value={address.city}
                            onChange={(e) => handleAddressChange('city', e.target.value)}
                            placeholder="Cidade"
                          />
                        </div>
                        <div>
                          <Label htmlFor="state">Estado</Label>
                          <Select value={address.state} onValueChange={(value) => handleAddressChange('state', value)}>
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="SP">São Paulo</SelectItem>
                              <SelectItem value="RJ">Rio de Janeiro</SelectItem>
                              <SelectItem value="MG">Minas Gerais</SelectItem>
                              <SelectItem value="RS">Rio Grande do Sul</SelectItem>
                              {/* Add more states */}
                            </SelectContent>
                          </Select>
                        </div>
                      </div>
                    </div>
                  )}

                  {useProfileAddress && (
                    <div className="bg-gray-50 p-4 rounded-lg">
                      <h4 className="font-medium mb-2">Endereço Selecionado:</h4>
                      <p className="text-sm text-gray-600">
                        Rua das Flores, 123, Apto 45<br />
                        Centro - São Paulo, SP<br />
                        01234-567
                      </p>
                    </div>
                  )}

                  <Button 
                    onClick={() => setCurrentStep(2)}
                    className="w-full bg-red-600 hover:bg-red-700 text-white"
                  >
                    Continuar para Pagamento
                  </Button>
                </CardContent>
              </Card>
            )}

            {/* Step 2: Payment */}
            {currentStep === 2 && (
              <Card>
                <CardHeader>
                  <CardTitle>Forma de Pagamento</CardTitle>
                </CardHeader>
                <CardContent>
                  <Tabs value={paymentMethod} onValueChange={setPaymentMethod}>
                    <TabsList className="grid w-full grid-cols-3">
                      <TabsTrigger value="card">Cartão</TabsTrigger>
                      <TabsTrigger value="pix">PIX</TabsTrigger>
                      <TabsTrigger value="boleto">Boleto</TabsTrigger>
                    </TabsList>

                    {/* Credit Card */}
                    <TabsContent value="card" className="space-y-4 mt-6">
                      <div>
                        <Label htmlFor="cardNumber">Número do Cartão</Label>
                        <Input
                          id="cardNumber"
                          value={cardData.number}
                          onChange={(e) => handleCardChange('number', e.target.value)}
                          placeholder="0000 0000 0000 0000"
                          maxLength={19}
                        />
                      </div>

                      <div>
                        <Label htmlFor="cardName">Nome no Cartão</Label>
                        <Input
                          id="cardName"
                          value={cardData.name}
                          onChange={(e) => handleCardChange('name', e.target.value)}
                          placeholder="Nome como está no cartão"
                        />
                      </div>

                      <div className="grid grid-cols-3 gap-4">
                        <div>
                          <Label htmlFor="cardExpiry">Validade</Label>
                          <Input
                            id="cardExpiry"
                            value={cardData.expiry}
                            onChange={(e) => handleCardChange('expiry', e.target.value)}
                            placeholder="MM/AA"
                            maxLength={5}
                          />
                        </div>
                        <div>
                          <Label htmlFor="cardCvv">CVV</Label>
                          <Input
                            id="cardCvv"
                            value={cardData.cvv}
                            onChange={(e) => handleCardChange('cvv', e.target.value)}
                            placeholder="000"
                            maxLength={4}
                          />
                        </div>
                        <div>
                          <Label htmlFor="installments">Parcelas</Label>
                          <Select value={cardData.installments} onValueChange={(value) => handleCardChange('installments', value)}>
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="1">1x R$ {total.toFixed(2)}</SelectItem>
                              <SelectItem value="2">2x R$ {(total / 2).toFixed(2)}</SelectItem>
                              <SelectItem value="3">3x R$ {(total / 3).toFixed(2)}</SelectItem>
                              <SelectItem value="6">6x R$ {(total / 6).toFixed(2)}</SelectItem>
                              <SelectItem value="12">12x R$ {(total / 12).toFixed(2)}</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>
                      </div>
                    </TabsContent>

                    {/* PIX */}
                    <TabsContent value="pix" className="space-y-4 mt-6">
                      <div className="text-center py-8">
                        <div className="text-6xl mb-4">📱</div>
                        <h3 className="text-lg font-semibold mb-2">Pagamento via PIX</h3>
                        <p className="text-gray-600 mb-4">
                          Após confirmar o pedido, você receberá o código PIX para pagamento
                        </p>
                        <div className="bg-green-50 p-4 rounded-lg">
                          <p className="text-green-800 font-medium">
                            ✓ Aprovação instantânea
                          </p>
                          <p className="text-green-600 text-sm">
                            Seu pedido será processado imediatamente após o pagamento
                          </p>
                        </div>
                      </div>
                    </TabsContent>

                    {/* Boleto */}
                    <TabsContent value="boleto" className="space-y-4 mt-6">
                      <div className="text-center py-8">
                        <div className="text-6xl mb-4">🧾</div>
                        <h3 className="text-lg font-semibold mb-2">Boleto Bancário</h3>
                        <p className="text-gray-600 mb-4">
                          O boleto será enviado por email e pode ser pago em qualquer banco
                        </p>
                        <div className="bg-yellow-50 p-4 rounded-lg">
                          <p className="text-yellow-800 font-medium">
                            ⚠️ Prazo de pagamento: 3 dias úteis
                          </p>
                          <p className="text-yellow-600 text-sm">
                            O pedido será processado após a confirmação do pagamento
                          </p>
                        </div>
                      </div>
                    </TabsContent>
                  </Tabs>

                  <div className="flex space-x-4 mt-6">
                    <Button 
                      variant="outline" 
                      onClick={() => setCurrentStep(1)}
                      className="flex-1"
                    >
                      Voltar
                    </Button>
                    <Button 
                      onClick={() => setCurrentStep(3)}
                      disabled={!paymentMethod}
                      className="flex-1 bg-red-600 hover:bg-red-700 text-white"
                    >
                      Revisar Pedido
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )}

            {/* Step 3: Confirmation */}
            {currentStep === 3 && (
              <Card>
                <CardHeader>
                  <CardTitle>Confirmar Pedido</CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  {/* Address Summary */}
                  <div>
                    <h4 className="font-medium mb-2">Endereço de Entrega:</h4>
                    <div className="bg-gray-50 p-4 rounded-lg text-sm">
                      <p>{mockUser.name}</p>
                      <p>Rua das Flores, 123, Apto 45</p>
                      <p>Centro - São Paulo, SP</p>
                      <p>01234-567</p>
                    </div>
                  </div>

                  {/* Payment Summary */}
                  <div>
                    <h4 className="font-medium mb-2">Forma de Pagamento:</h4>
                    <div className="bg-gray-50 p-4 rounded-lg text-sm">
                      {paymentMethod === 'card' && (
                        <p>Cartão de Crédito - {cardData.installments}x de R$ {(total / parseInt(cardData.installments)).toFixed(2)}</p>
                      )}
                      {paymentMethod === 'pix' && <p>PIX - Pagamento à vista</p>}
                      {paymentMethod === 'boleto' && <p>Boleto Bancário - Vencimento em 3 dias</p>}
                    </div>
                  </div>

                  {/* Terms */}
                  <div className="flex items-start space-x-2">
                    <Checkbox id="terms" />
                    <Label htmlFor="terms" className="text-sm">
                      Concordo com os{' '}
                      <Link href="/terms" className="text-red-600 hover:underline">
                        termos de uso
                      </Link>{' '}
                      e{' '}
                      <Link href="/privacy" className="text-red-600 hover:underline">
                        política de privacidade
                      </Link>
                    </Label>
                  </div>

                  <div className="flex space-x-4">
                    <Button 
                      variant="outline" 
                      onClick={() => setCurrentStep(2)}
                      className="flex-1"
                      disabled={isProcessing}
                    >
                      Voltar
                    </Button>
                    <Button 
                      onClick={processPayment}
                      disabled={isProcessing}
                      className="flex-1 bg-red-600 hover:bg-red-700 text-white"
                    >
                      {isProcessing ? 'Processando...' : 'Finalizar Pedido'}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Order Summary Sidebar */}
          <div>
            <Card className="sticky top-4">
              <CardHeader>
                <CardTitle>Resumo do Pedido</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Items */}
                <div className="space-y-3">
                  {mockOrderItems.map((item) => (
                    <div key={item.id} className="flex items-center space-x-3">
                      <div className="relative w-12 h-12 flex-shrink-0">
                        <Image
                          src={item.image}
                          alt={item.name}
                          fill
                          className="object-cover rounded"
                        />
                        <Badge className="absolute -top-2 -right-2 bg-red-500 text-white text-xs w-5 h-5 flex items-center justify-center p-0">
                          {item.quantity}
                        </Badge>
                      </div>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium truncate">{item.name}</p>
                        <p className="text-xs text-gray-500">{item.size} • {item.color}</p>
                        <p className="text-sm font-medium">R$ {item.price.toFixed(2)}</p>
                      </div>
                    </div>
                  ))}
                </div>

                <Separator />

                {/* Totals */}
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span>Subtotal</span>
                    <span>R$ {subtotal.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span>Frete</span>
                    <span>R$ {shipping.toFixed(2)}</span>
                  </div>
                  <Separator />
                  <div className="flex justify-between font-bold">
                    <span>Total</span>
                    <span className="text-red-600">R$ {total.toFixed(2)}</span>
                  </div>
                </div>

                {/* Delivery Info */}
                <div className="bg-green-50 p-3 rounded-lg">
                  <p className="text-sm font-medium text-green-800">
                    🚚 Entrega estimada: 30-45 min
                  </p>
                  <p className="text-xs text-green-600">
                    Após a confirmação do pagamento
                  </p>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </Layout>
  )
}
